html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.section-offset {
  padding-top: 120px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1260px;
}

.main-title {
  text-transform: uppercase;
  color: #1e212c;
  font-weight: 700;
  font-size: 4.5rem;
  line-height: 1.3;
  letter-spacing: 1px;
  margin: 0 0 1.5rem 0;
}
.main-title--home {
  color: #fff;
}
.main-title--home span {
  color: #FF5A30;
}

.main-descr {
  margin: 0;
  font-size: 1.25rem;
  line-height: 1.5;
  color: #787a80;
}
.main-descr--home {
  color: #fff;
}

.btn {
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  border-radius: 4px;
  border: 1px solid transparent;
  letter-spacing: 0.5px;
}
.btn--small {
  font-size: 0.75rem;
  line-height: 36px;
  padding: 0 24px;
}
.btn--regular {
  font-size: 0.875rem;
  line-height: 42px;
  padding: 0 30.5px;
}
.btn--large {
  font-size: 1rem;
  line-height: 50px;
  padding: 0 38.9px;
}
.btn--stroke {
  border: 1px solid #fff;
}
.btn--primary {
  border-color: #FF5A30;
  color: #FF5A30;
}
.btn--fill {
  background-color: #FF5A30;
}

.title {
  color: #1e212c;
  font-size: 2.875rem;
  font-weight: 700;
  line-height: 1.3;
  margin: 0 0 1.5rem 0;
}
.title--center {
  text-align: center;
}

.descr {
  margin: 0 0 60px 0;
  font-size: 1.125rem;
  line-height: 1.5;
  color: #787a80;
}
.descr--center {
  text-align: center;
}

.header {
  padding: 23px 0 21px 0;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header--main {
  background-color: #fff;
}
.header__nav {
  margin-left: -116px;
}

.hero {
  color: #fff;
  background-color: rgb(131, 131, 131);
  background-image: url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 220px 0 353px 0;
}
.hero__content {
  max-width: 573px;
}
.hero__btns {
  display: flex;
  column-gap: 24px;
  margin-top: 62px;
}

.about-section {
  padding-bottom: 60px;
  background: #fff;
}

.mission {
  background-color: #fff;
}
.mission__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 188px;
  margin-bottom: 33px;
}
.mission__item {
  text-align: center;
  padding-top: 74px;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 48px 48px;
  position: relative;
}
.mission__item:not(:last-child)::after {
  content: "";
  background-image: url("../img/divider.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 1px;
  height: 100%;
  position: absolute;
  top: 0;
  right: -94px;
}
.mission__item--quality {
  background-image: url("../img/quality.svg");
}
.mission__item--safety {
  background-image: url("../img/safety.svg");
}
.mission__item--comfort {
  background-image: url("../img/comfort.svg");
}
.mission__subtitle {
  font-size: 20px;
  font-weight: 700;
  color: #1e212c;
}
.mission__descr {
  font-size: 16px;
  line-height: 1.6;
  margin-top: 13px;
  color: #787a80;
}

.know-form {
  position: relative;
  top: 92px;
  padding: 48px 60px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
}
.know-form__title {
  font-size: 28px;
  font-weight: 700;
  color: #1e212c;
  text-align: center;
  margin-bottom: 40px;
}
.know-form__content {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  column-gap: 24px;
}

.services-section {
  padding: 212px 0 80px 0;
  background-image: url("../img/services-section-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.services-section__services-list {
  margin-bottom: 78px;
}

.services-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.services-item {
  position: relative;
  border: 1px solid #e5e8ed;
  border-radius: 4px;
  box-shadow: 0 80px 80px -20px rgba(154, 156, 165, 0.08), 0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 12px 10px -6px rgba(154, 156, 165, 0.04), 0 4px 4px -4px rgba(30, 33, 44, 0.03);
  background: #fff;
}
.services-item::before, .services-item::after {
  content: "";
  position: absolute;
  opacity: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: inherit;
}
.services-item::before {
  background-color: #1e212c;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
}
.services-item--design::before {
  background-image: url("../img/services-section-card-3.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.services-item::after {
  background-color: #1e212c;
  z-index: 5;
}
.services-item:hover::before {
  opacity: 1;
}
.services-item:hover::after {
  opacity: 0.6;
}
.services-item:hover .services-item__icon {
  fill: #fff;
}
.services-item:hover .services-item__title {
  color: #fff;
}
.services-item__wrap {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 89px 10px 90px 10px;
}
.services-item__icon {
  width: 52px;
  height: 52px;
  object-fit: cover;
  fill: #FF5A30;
  transition: fill 0.3s ease-in-out;
}
.services-item__title {
  font-size: 20px;
  font-weight: 700;
  color: #1e212c;
  line-height: 1.5;
  margin-top: 24px;
  transition: color 0.3s ease-in-out;
}

.portfolio-section {
  background-color: #fff;
  padding-bottom: 120px;
}
.portfolio-section__top {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 47px;
}
.portfolio-section__title {
  margin: 0;
  padding-bottom: 12px;
}
.portfolio-section__portfolio-slider {
  margin-bottom: 80px;
}

.portfolio-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  padding-bottom: 23px;
  border-radius: 4px;
  box-shadow: 0 80px 80px -20px rgba(154, 156, 165, 0.08), 0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 12px 10px -6px rgba(154, 156, 165, 0.04), 0 4px 4px -4px rgba(30, 33, 44, 0.03);
  background: #fff;
}
.portfolio-item--active .btn {
  display: block;
}
.portfolio-item--active img {
  height: 277px;
}
.portfolio-item__img {
  border-radius: inherit;
  height: 345px;
  width: 390px;
}
.portfolio-item__title {
  font-size: 20px;
  font-weight: 700;
  color: #1e212c;
  line-height: 1.5;
  margin-top: 16px;
}
.portfolio-item__descr {
  font-size: 14px;
  line-height: 1.5;
  color: #787a80;
  margin-top: 5px;
}
.portfolio-item__btn {
  display: none;
  margin-top: 23px;
}

.portfolio-slider__wrapper {
  display: flex;
  justify-content: space-between;
}

.nav {
  font-size: 1rem;
}
.nav__list {
  display: flex;
  column-gap: 40px;
}
.nav__link {
  color: #424551;
  font-weight: 700;
  line-height: 1.6;
}

.header-contacts {
  display: flex;
  column-gap: 36px;
}
.header-contacts__link {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contacts-link {
  padding-left: 52px;
  background-position: left center;
  background-size: 40px 40px;
  background-repeat: no-repeat;
}
.contacts-link__caption {
  color: #424551;
  font-weight: 700;
  font-size: 0.875rem;
  line-height: 1.5;
}
.contacts-link__value {
  color: #1e212c;
  font-size: 1.125rem;
  line-height: 1.5;
}
.contacts-link--call {
  background-image: url("../img/phone.svg");
}
.contacts-link--mail {
  background-image: url("../img/mail.svg");
}

.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  height: 500px;
}
.video-block::before {
  content: "";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(30, 33, 44, 0.6);
  position: absolute;
  z-index: 3;
}
.video-block__content {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-block__play {
  position: absolute;
  width: 80px;
  height: 80px;
  background-color: #FF5A30;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.video-block__play svg {
  width: 21px;
  height: 22px;
}
.video-block__muted {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.12);
  top: 85.9%;
  left: 5.4%;
  transform: translate(-85.9%, -5.5%);
  z-index: 5;
}
.video-block__muted svg {
  width: 20px;
  height: 20px;
  color: #ffffff;
}

.form-field--small input {
  width: 260px;
}
.form-field--regular input {
  width: 364px;
}
.form-field--large input {
  width: 415px;
}
.form-field__caption {
  display: block;
  font-size: 14px;
  color: #424551;
  margin-bottom: 10px;
}
.form-field__input {
  display: block;
  background-color: #f4f5f7;
  padding: 12px 0 12px 16px;
  border-radius: 4px;
  border: 1px solid #d7dadd;
}
.form-field__input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #9a9ca5;
}

.slider-nav {
  display: flex;
}
.slider-nav__btn {
  width: 48px;
  height: 48px;
  color: #424551;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.slider-nav__btn svg {
  display: block;
  width: 18px;
  height: 11px;
  color: inherit;
  fill: currentColor;
  object-fit: cover;
}
.slider-nav__btn:hover {
  background-color: #FF5A30;
  color: #fff;
}

.see-more {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 40px;
}
.see-more__subtitle {
  font-weight: 700;
  font-size: 28px;
  line-height: 1.5;
  color: #1e212c;
}